@import url(https://fonts.googleapis.com/css?family=Montserrat);
html, body, #root {
  height: 100%;
}

html {
  /* Fallback background */
  /* background: #364b6d;  
  background: linear-gradient(115deg, #373b44, #4286f4); */
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
nav {
  display: flex;
  text-align: center;
  justify-content: space-between;
  border-bottom: 1px solid lightblue;
  align-items: center;
}

.nav-title {
  padding: 1rem 1.5rem;
  font-size: 1.5rem;
}

.nav-links {
  display: flex;
  padding: 0.5rem 1rem;
  margin: 0;
}

.nav-title:hover, .nav-links > li:hover {
  cursor: pointer;
  color: grey;
}

footer {
  margin: 0.5rem 0;
}
.dashboard--item {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  text-align: center;
  background: rgb(246, 249, 252);
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  box-shadow: 0.25rem 0.25rem 0.25rem darkslategrey;
  padding: 0.5rem;
  width: 80%;
  max-width: 200px;
  height: auto;
  font-size: 0.8rem;
}

.dashboard--item:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: 0.8s;
  transition: 0.8s;
  cursor: pointer;
}
.dashboard-title {
  padding-bottom: 4rem;
}

.dashboard-menu {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 1rem;
  padding: 2rem 0;
  justify-items: center;
  justify-content: center;
  max-width: 60%;
  margin: 0 auto;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: rgb(246, 249, 252);
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  box-shadow: 0.25rem 0.25rem 0.25rem darkslategrey;
  padding: 0.5rem;
  width: 80%;
  max-width: 400px;
  height: auto;
}

.card:hover {
  cursor: pointer;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  -webkit-transition: 0.8s;
  transition: 0.8s;
}

.card--title {
  padding: 0.75rem 1.25rem;
  text-align: center;
}

.card--content {
  padding: 0.5rem 0.5rem 0.25rem;
  font-size: 0.9rem;
  text-align: left;
}

.card--label {
  font-size: 0.9rem;
  display: inline-block;
}

.card--button {
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  padding: 0.5rem 1rem;
  margin: 0;
  text-decoration: none;
  background: #0069ed;
  color: #ffffff;
  font-family: sans-serif;
  font-size: 1rem;
  cursor: pointer;
  text-align: center;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.card--button--container {
  display: flex;
  justify-content: center;
  align-self: center;
  padding: 0.25rem 0 0;
}

.rotten-tomato {
  width: 1rem;
  height: 1rem;
}

