.card-expanded--bg {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  justify-content: center;
}

.card-expanded {
  align-self: center;
  background: rgb(246, 249, 252);
  border: 1px solid;
  border-color: lightblue;
  border-radius: 0.25rem;
  box-shadow: 0.25rem 0.25rem 0.25rem darkslategrey;
  padding: 0.5rem;
  width: 50%;
  max-width: 600px;
  height: auto;
}

.card-expanded:hover {
  cursor: pointer;
}

.card-expanded .card--title {
  padding: 1rem;
  margin: 0;
  font-size: 1.4rem;
}

.card-expanded .card--content {
  padding: 0.5rem 1rem;
  font-size: 1rem;
  text-align: left;
}

.card-expanded .card--label {
  font-size: 1rem;
  display: inline-block;
}

.card-expanded .card--button--container {
  display: flex;
  justify-content: center;
  align-self: center;
  padding: 0.5rem 0 0;
}
.card-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1rem;
  padding: 2rem;
  justify-items: center;
  max-width: 80%;
  margin: 0 auto;
}

.category--title {
  padding-top: 2rem;
  margin: 0;
}

.container {
  height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
  font-family: "Montserrat";
  text-align: center;
  /* background: linear-gradient(115deg, #373b44, #4286f4); */
}

.content {
  align-self: center;
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

ul {
  list-style: none;
}

